export default {
	data() {
		return {
			list:[],
			keywords:'',
			nav_0:true,
			nav_1:false,
			type:2,
		}
	},
	onLoad(e) {
		var t=this;	
		t.getOamissiveList();
	},
	methods: {
		planDetail:function(id){
			uni.navigateTo({
				url: 'zhibu-work-plan-detail?id='+id
			})	
		},
		search:function(){
			uni.showLoading();
			var t=this;
			var data={
				communist_no:t.globalData.userInfo.communist_no,
				keywords:t.keywords,
				type:t.type,
			}
			t.urlRequest.urlRequest(t.func.oaWorkPlanList,data,function(res){
				t.list=res.data.workplan_list;
				uni.hideLoading();
			})
		},
		getOamissiveList:function(){
			uni.showLoading();
			var t=this;
			var data={
				communist_no:t.globalData.userInfo.communist_no,
				keywords:t.keywords,
				type:t.type,
			}
			t.urlRequest.urlRequest(t.func.oaWorkPlanList,data,function(res){
				t.list=res.data.workplan_list;
				uni.hideLoading();
			})
		},
		navChange:function(type){
			var t=this;
			if(type==0){
				t.nav_0=true;
				t.nav_1=false;
				t.type=2;
				t.getOamissiveList();
			}else{
				t.nav_1=true;
				t.nav_0=false;
				t.type=1;
				t.getOamissiveList();
			}
		}
	}
}
